@import "../../css-config/mixins.scss";
@import url('https://fonts.googleapis.com/css2?family=Arima:wght@500;600&family=Chela+One&family=Clicker+Script&family=Edu+NSW+ACT+Foundation:wght@500&family=Kaushan+Script&family=Mali:wght@300;400&family=Merriweather:ital,wght@1,300&family=Patua+One&family=Play:wght@700&family=Righteous&family=Rubik+Moonrocks&family=Satisfy&display=swap');

.main-header{
    width:100%;
    padding: 0 20px;
    background-color: #04aa6d; ;
    color: rgb(31, 31, 31);
    border-bottom:  1px solid rgba(0,0,0,0.1);
    z-index:99;
    @include md{
        padding: 0 40px;
    }
    .header-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md{
            height: 80px;
        }
        .left{
            list-style-type: none;
            display: none;
            gap: 25px;
            @include md{
                display: flex;
            }
            li{
                font-size: 14px;
                font-weight: 600;
                text-transform: uppercase;
                cursor: pointer;
                font-family: 'Arima', cursive;

            }
        }
        .centre{
            font-size: 22px;
            font-weight: 700;
            cursor: pointer;
            font-family: 'Rubik Moonrocks', cursive;
            @include md{
                font-size: 34px;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }
        }
        .right{
            display: flex;
            align-items: center;
            gap: 20px;
            @include md{
                gap: 25px;
            }
            svg{
                font-size: 20px;
                cursor: pointer;
                @include md{
                    font-size: 25px;
                }
            }
            .cart-icon{
                position: relative;
                span{
                    min-width: 20px;
                    text-align: center;
                    background-color: black;
                    color: white;
                    padding: 2.5px;
                    position: absolute;
                    top: -5px;
                    right: -12px;
                    font-size: 12px;
                    line-height: 1;
                    border-radius: 10px;
                }
            }
        }
    }
    &.sticky-header{
        position: sticky;
        top:0;
        animation: stickyHeader 0.3s ease forwards;
    }
}

@keyframes stickyHeader {
    0% {transform: translateY(-80px);}
    100% {transform: translateY(0);}
}