@import "../../css-config/mixins.scss";
@import url('https://fonts.googleapis.com/css2?family=Arima:wght@500;600&family=Chela+One&family=Clicker+Script&family=Edu+NSW+ACT+Foundation:wght@500&family=Kaushan+Script&family=Lobster+Two:ital,wght@1,700&family=Mali:wght@300;400&family=Merriweather:ital,wght@1,300&family=Patua+One&family=Play:wght@700&family=Righteous&family=Rubik+Moonrocks&family=Satisfy&display=swap');

.single-product-main-content{
    margin: 20px 0;
    @include md{
        margin: 75px 0;
    }
    .layout{
        max-width: calc(100% - 20px);
        margin: 0 auto;
        @include md{
            max-width: 1200px;
        }
        .single-product-page{
            display: flex;
            flex-direction: column;
            @include md{
                flex-direction: row;
            }
            .left{
                width: 100%;
                background-color: rgb(4, 170, 109,0.05);
                flex-shrink: 0;
                @include md{
                    width: 400px;
                    height: 400px;
                }
                img{
                    width: 100%;
                    display: block;
                }
            }
            .right{
                display: flex;
                flex-direction: column;
                padding-top: 20px;
                @include md{
                    padding: 0 35px;
                }
                .name{
                    font-family: 'Arima', cursive;
                    font-size: 20px;
                    line-height: 28px;
                    margin-bottom: 20px;
                    @include md{
                        font-size: 24px;
                        line-height: 32px;
                    }
                }
                .price{
                    font-size: 24px;
                    line-height: 32px;
                    margin-bottom: 20px;
                }
                .desc{
                    font-size: 14px;
                    line-height: 20px;
                    margin-bottom: 20px;
                    color: rgb(4, 170, 109,0.9);
                    @include md{
                        font-size: 16px;
                        line-height: 24px;
                    }
                }
                .cart-buttons{
                    display: flex;
                    @include md{
                        margin-top: 30px;
                    }
                    .quantity-buttons{
                        width: fit-content;
                        display: flex;
                        border: 2px solid rgb(4, 170, 109,0.2);
                        margin-right: 10px;
                        height:50px;
                        span{
                            font-size: 18px;
                            width: 40px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            cursor: pointer;
                            color:rgb(4, 170, 109,0.9);
                            &:nth-child(1){
                                border-right: 2px solid rgb(4, 170, 109,0.2);
                            }
                            &:nth-child(2){
                                cursor: context-menu;
                                width: 60px;
                            }
                            &:nth-child(3){
                                border-left: 2px solid rgb(4, 170, 109,0.2);
                            }
                        }
                    }
                    .add-to-cart-button{
                        outline: 0;
                        border: 0;
                        height: 50px;
                        width: 180px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        color: blueviolet;
                        cursor: pointer;
                        font-size: 16px;
                        background: rgb(167, 210, 255,0.5);
                        border-bottom: 2px solid rgb(167, 210, 255);
                        border-right: 2px solid rgb(167, 210, 255);
                        flex-grow: 1;
                        @include md{
                            flex-grow: unset;
                        }
                    }
                }
                .divider{
                    margin: 20px 0;
                    height: 1px;
                    width: 100%;
                    background-color: rgba($color: #000000, $alpha: 0.1);
                }
                .info-item{
                    .text-bold{
                        font-size: 18px;
                        font-weight: 500;
                        display: block;
                        &:nth-child(1){
                            margin-bottom: 20px;
                            
                        }
                        span{
                            font-size: 16px;
                            font-weight: 400;
                            cursor: pointer;
                            color: rgb(4, 170, 109,0.8);
                            
                        }
                        svg{
                            margin: 0 5px;
                            transition: all ease 0.1s;
                            &:hover{
                                color: rgba($color: #000000, $alpha: 0.5);
                                transform: scale(1.2);
                            }
                        }
                    }
                }
            }
        }
    }
}