@import "../../css-config/mixins.scss";
@import url('https://fonts.googleapis.com/css2?family=Arima:wght@500;600&family=Clicker+Script&family=Edu+NSW+ACT+Foundation:wght@500&family=Kaushan+Script&family=Mali:wght@300;400&family=Merriweather:ital,wght@1,300&family=Patua+One&family=Play:wght@700&family=Righteous&family=Rubik+Moonrocks&family=Satisfy&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Arima:wght@500;600&family=Clicker+Script&family=Edu+NSW+ACT+Foundation:wght@500&family=Kaushan+Script&family=Mali:wght@300;400&family=Merriweather:ital,wght@1,300&family=Open+Sans&family=Patua+One&family=Play:wght@700&family=Righteous&family=Rubik+Moonrocks&family=Satisfy&display=swap');

.cart-pannel{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    z-index: 99;
    .opac-layer{
        background-color: rgba($color: #000000, $alpha: 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .cart-content{
        width: 100%;
        height: 100%;
        background: white;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        transform: translateX(100%);
        animation: slide 0.5s ease forwards;
        @include md{
            width: 340px;
        }
        .cart-header{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding: 20px 15px;
            border-bottom: 1px solid rgba($color: #000000, $alpha: 0.5);
            .heading{
                flex-grow: 1;
                margin-bottom: 0;
                font-size: 20px;
                font-weight: 700;
                color: #04aa6d;
                font-family: 'Arima', cursive;
            }
            .close-btn{
                display: flex;
                align-items: center;
                cursor: pointer;
                
                svg{
                    font-size: 18px;
                }
                .text{
                    text-transform: uppercase;
                    font-size: 14px;
                }
                &:hover{
                    opacity: .5;
                }
            }
        }
        .empty-cart{
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            margin-top: 100px;
            svg{
                font-size: 120px;
                opacity: 0.1;
            }
            .return-btn{
                height: 40px;
                width: 150px;
                text-transform: uppercase;
                font-weight: 500;
                border: 2px solid black;
                padding: 5px 10px;
                cursor: pointer;
                background-color: #04aa6d;
                color: white;
                font-size: 20px;
                &:hover{
                    background-color: white;
                    color: #04aa6d;
                    }
            }
        }
        .cart-footer{
            border-top: 1px solid rgba($color: #000000, $alpha: 0.5);
            .subtotal{
                padding: 20px 15px;
                border-bottom: 1px solid rgba($color: #000000, $alpha: 0.5);
                display: flex;
                justify-content: space-between;
                .text{
                    font-family: 'Arima', cursive;
                    margin-bottom: 0;
                    font-size: 20px;
                    font-weight: 700;
                    text-transform: uppercase;
                    &.total{
                        color: #04aa6d;
                    }
                }

            }
            .button{
                padding: 20px 15px;
                .checkout-cta{
                    height: 50px;
                    width: 100%;
                    text-transform: uppercase;
                    font-weight: 500;
                    border: 2px solid black;
                    padding: 5px 10px;
                    cursor: pointer;
                    background-color: #04aa6d;
                    color: white;
                    font-size: 16px;
                    &:hover{
                        background-color: white;
                        color: #04aa6d;
                    }
                }
            }
        }
    }
}
@keyframes slide {
    0%{transform: translateX(100%);}
    100%{transform: translateX(0%);}
}
